body {
  margin: 0;
  font-family:Monospace, -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(to right, rgb(9, 88, 236), rgb(255, 0, 212));
}

code {
  font-family: Monospace, source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
