.bounding-box{
    position: absolute;
    box-shadow: 0 0 0 3px #149df2 inset;
    display: -ms-flexbox;/*comment it out*/
    display: flex;
    -ms-flex-wrap: wrap;/*this one too*/
    flex-wrap: wrap;
    -ms-flex-pack: center;/*this one too*/
    justify-content: center;
    cursor: pointer;
}